import { MEDIA_CONNECTION_CHANGE } from '../../call/interface/eventTypes'
import { getCallByWebrtcSessionId } from '../../call/interface/selectors'
import * as webrtcActionTypes from '../../webrtc/interface/actionTypes'

export default function getAssociatedCallEvent (context, action) {
  switch (action.type) {
    case webrtcActionTypes.SESSION_ICE_CONNECTION_STATE_CHANGE: {
      const call = getCallByWebrtcSessionId(context.getState(), action.payload.id)
      if (call) {
        return {
          type: MEDIA_CONNECTION_CHANGE,
          args: { callId: call.id, previous: { state: call.mediaConnectionState } }
        }
      }
    }
  }
}
