// The factory funnctions for the negotiation handlers.
import handleSlowUpdateRequest from './handleSlowUpdateRequest'
import handleSlowUpdateResponse from './handleSlowUpdateResponse'

import handleUpdateRequest from './handleUpdateRequest'

export default function initOperation (bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `notification` sub-container.
  bottle.factory('Callstack.notifications.handleSlowUpdateRequest', () => handleSlowUpdateRequest(bottle.container))
  bottle.factory('Callstack.notifications.handleSlowUpdateResponse', () => handleSlowUpdateResponse(bottle.container))
  bottle.factory('Callstack.notifications.handleUpdateRequest', () => handleUpdateRequest(bottle.container))
}
