import { REPORTER_EVENTS, REPORTER_METRICS, REPORTER_OPERATION_EVENTS_MAP } from './constants'

/**
 * @method durationHandler
 * @param {string} metric The metric type which will be added to report when handler function is executed.
 * @param {Array} startEvents The set of event types whose `start` timestamp will be used to calculate the metric value.
 * @param {boolean} autoUnregister Specifies wether to automatically unregister the event handler function after it executed once.
 *   By default (if not specified), it will unregister the handler once it executed.
 * @returns {undefined}
 */
function durationHandler (metric, startEvents, autoUnregister = true) {
  /**
   * Handler function which executes when a certain registered event has ended.
   * @return {boolean} Returns true if handler was executed AND metric was saved. False otherwise.
   */
  return function (callReport, event) {
    // First search for the event in the timeline of the report
    // i.e. see if it's a top-level event
    // We need to find the last type of that event because in the case of
    // mutiple HOLD/UNHOLD operations, the metric calculated for it needs to
    // use the last entry.
    const startEvent = callReport.timeline.findLast(event => {
      if (startEvents.includes(event.type)) {
        return true
      }
      // We didn't find it in top-level events, search within the sub-events
      return event.timeline.find(subEvent => {
        return startEvents.includes(subEvent.type)
      })
    })

    if (startEvent) {
      if (
        event.type === REPORTER_EVENTS.PROCESS_RESPONSE &&
        metric === 'MAKE_CALL_REMOTE_SETUP' &&
        event.getData('operation') !== 'MAKE'
      ) {
        // PROCESS_RESPONSE event is triggered as part of many SDK operations and on both sides of the call.
        // But we don't want to add a 'MAKE_CALL_REMOTE_SETUP' metric for the callee side,
        // because it only makes sense for caller side.
        return
      }
      // We found the start event, so save its associated metric containing the measurement.
      callReport.addMetric(metric, event.end - startEvent.start)
      if (autoUnregister) {
        // We automatically unregister the handler once it was executed.
        callReport.unregisterMetricHandler(metric)
      }
      return true
    }
    return false
  }
}

/**
 * @method joinedCallDurationHandler
 * @param {string} metric The metric type which will be added to report when handler function is executed.
 * @param {Array} startEvents The set of event types whose `start` timestamp will be used to calculate the metric value.
 * @param {boolean} autoUnregister Specifies wether to automatically unregister the event handler function after it executed once.
 *   By default (if not specified), it will unregister the handler once it executed.
 * @returns {undefined}
 */
function joinedCallDurationHandler (metric, startEvents, autoUnregister = true) {
  /**
   * Handler function which executes when a certain registered event has ended.
   * @return {boolean} Returns true if handler was executed AND metric was saved. False otherwise.
   */
  return function (callReport, event) {
    // First search for the event in the timeline of the report
    // i.e. see if it's a top-level event
    const startEvent = callReport.timeline.find(event => {
      if (startEvents.includes(event.type)) {
        return true
      }
      // We didn't find it in top-level events, search within the sub-events
      return event.timeline.find(subEvent => {
        return startEvents.includes(subEvent.type)
      })
    })
    // startEvent !== event is for the special case join operation which is used as the end for the initial
    // two calls, but the starting event for the joined call.
    if (startEvent && startEvent !== event) {
      // We found the start event, so save its associated metric containing the measurement.
      callReport.addMetric(metric, event.end - startEvent.start)
      if (autoUnregister) {
        // We automatically unregister the handler once it was executed.
        callReport.unregisterMetricHandler(metric)
      }
      return true
    }
    return false
  }
}

/**
 * A function that gathers the relay candidates as they are collected and then returns a handler function that will
 * create the metric for the relay candidates after collection is complete.
 *
 * Each time a relay candidate collected event is received it will create an object with that relay candidates data and
 * store it in an array. When the set local description event ends it will update all the collected candidates in the array
 * with the correct duration, and add a metric to the call report for them.
 * @method relayCandidatesHandler
 * @returns {Function} The metric handler function
 */
function relayCandidatesHandler () {
  const collectedCandidates = []
  return (callReport, event) => {
    if (event.type === REPORTER_EVENTS.RELAY_CANDIDATE_COLLECTED) {
      const eventData = event.getData()
      const collectedCandidateInformation = {
        ...eventData,
        duration: event.start // The candidate was collected by the time this event was added
      }
      collectedCandidates.push(collectedCandidateInformation)
    } else if (event.type === REPORTER_EVENTS.SET_LOCAL_DESCRIPTION) {
      // Update the duration of all the collected candidates
      collectedCandidates.map(candidateInfo => {
        candidateInfo.duration = candidateInfo.duration - event.start
        return collectedCandidates
      })
      // Only need to add metric if there will be any data...
      if (collectedCandidates.length) {
        callReport.addMetric(REPORTER_METRICS.TIME_TO_RELAY_CANDIDATES, {
          operation: event.getData('operation'),
          candidates: [...collectedCandidates]
        })
      }
      collectedCandidates.length = 0
    }
  }
}

// Generic function to register all metric handlers to the call reporter
function registerAllMetricHandlers (callReport) {
  // Register the call duration handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.CALL_DURATION,
    [
      REPORTER_EVENTS.END_LOCAL,
      REPORTER_EVENTS.END_REMOTE,
      REPORTER_EVENTS.JOIN,
      REPORTER_EVENTS.DIRECT_TRANSFER,
      REPORTER_EVENTS.CONSULTATIVE_TRANSFER
    ],
    joinedCallDurationHandler(REPORTER_METRICS.CALL_DURATION, [
      REPORTER_EVENTS.MAKE,
      REPORTER_EVENTS.RECEIVE_CALL,
      REPORTER_EVENTS.JOIN
    ])
  )

  // Register the local-setup handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.MAKE_CALL_LOCAL_SETUP,
    [REPORTER_EVENTS.SET_LOCAL_DESCRIPTION],
    durationHandler(REPORTER_METRICS.MAKE_CALL_LOCAL_SETUP, [REPORTER_EVENTS.MAKE])
  )

  // Register the handler for collecting ice candidates during the local-setup.
  // The start event will be SET_LOCAL_DESCRIPTION and the metric will be saved when
  // its sub-event (i.e. ICE_COLLECTION) has ended.
  // Since SET_LOCAL_DESCRIPTION event occurs on both caller & callee, its sub-event: ICE_COLLECTION
  // will also be available for each party.
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_COLLECT_ICE_CANDIDATES,
    [REPORTER_EVENTS.ICE_COLLECTION],
    durationHandler(REPORTER_METRICS.TIME_TO_COLLECT_ICE_CANDIDATES, [REPORTER_EVENTS.SET_LOCAL_DESCRIPTION])
  )

  // Register the remote-setup handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.MAKE_CALL_REMOTE_SETUP,
    [REPORTER_EVENTS.PROCESS_RESPONSE],
    durationHandler(REPORTER_METRICS.MAKE_CALL_REMOTE_SETUP, [REPORTER_EVENTS.REST_REQUEST])
  )

  // Register the time-to-ringing handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_RINGING,
    [REPORTER_EVENTS.REMOTE_RINGING],
    durationHandler(REPORTER_METRICS.TIME_TO_RINGING, [REPORTER_EVENTS.MAKE])
  )

  // Register the time-to-answer handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_ANSWER,
    REPORTER_OPERATION_EVENTS_MAP.ANSWER,
    durationHandler(REPORTER_METRICS.TIME_TO_ANSWER, [REPORTER_EVENTS.MAKE, REPORTER_EVENTS.RECEIVE_CALL])
  )

  // Call time to media duration (answer operation starts until media is connected)
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_MEDIA_DURATION,
    REPORTER_EVENTS.SET_LOCAL_DESCRIPTION,
    durationHandler(REPORTER_METRICS.TIME_TO_MEDIA_DURATION, [REPORTER_OPERATION_EVENTS_MAP.ANSWER])
  )

  // Time to setup incoming call (incoming call notification until media is connected)
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_CALL_SETUP_DURATION,
    REPORTER_EVENTS.SET_LOCAL_DESCRIPTION,
    durationHandler(REPORTER_METRICS.TIME_TO_CALL_SETUP_DURATION, [REPORTER_EVENTS.RECEIVE_CALL])
  )

  // Time to reject incoming call
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_REJECT,
    [REPORTER_EVENTS.REJECT],
    durationHandler(REPORTER_METRICS.TIME_TO_REJECT, [REPORTER_EVENTS.REJECT])
  )

  // Register the time-to-ignore handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_IGNORE,
    [REPORTER_EVENTS.IGNORE],
    durationHandler(REPORTER_METRICS.TIME_TO_IGNORE, [REPORTER_EVENTS.IGNORE])
  )

  // Register the time-to-add-media handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_ADD_MEDIA,
    REPORTER_EVENTS.SET_LOCAL_DESCRIPTION,
    durationHandler(REPORTER_METRICS.TIME_TO_ADD_MEDIA, [REPORTER_OPERATION_EVENTS_MAP.ADD_MEDIA], false)
  )

  // Register the time-to-add-remote-media handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_ADD_MEDIA_REMOTE,
    REPORTER_EVENTS.SET_REMOTE_DESCRIPTION,
    durationHandler(REPORTER_METRICS.TIME_TO_ADD_MEDIA_REMOTE, [REPORTER_OPERATION_EVENTS_MAP.ADD_MEDIA_REMOTE], false)
  )

  // Register the time-to-hold-local handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_HOLD_LOCAL,
    [REPORTER_EVENTS.HOLD_LOCAL],
    durationHandler(REPORTER_METRICS.TIME_TO_HOLD_LOCAL, [REPORTER_EVENTS.HOLD_LOCAL], false)
  )

  // Register the time-to-hold-remote handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_HOLD_REMOTE,
    [REPORTER_EVENTS.HOLD_REMOTE],
    durationHandler(REPORTER_METRICS.TIME_TO_HOLD_REMOTE, [REPORTER_EVENTS.HOLD_REMOTE], false)
  )

  // Register the time-to-unhold-local handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_UNHOLD_LOCAL,
    [REPORTER_EVENTS.UNHOLD_LOCAL],
    durationHandler(REPORTER_METRICS.TIME_TO_UNHOLD_LOCAL, [REPORTER_EVENTS.UNHOLD_LOCAL], false)
  )

  // Register the time-to-unhold-remote handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_UNHOLD_REMOTE,
    [REPORTER_EVENTS.UNHOLD_REMOTE],
    durationHandler(REPORTER_METRICS.TIME_TO_UNHOLD_REMOTE, [REPORTER_EVENTS.UNHOLD_REMOTE], false)
  )

  // Register the time-to-remove-media handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_REMOVE_MEDIA,
    REPORTER_EVENTS.SET_LOCAL_DESCRIPTION,
    durationHandler(REPORTER_METRICS.TIME_TO_REMOVE_MEDIA, [REPORTER_OPERATION_EVENTS_MAP.REMOVE_MEDIA], false)
  )

  // Register the time-to-remove-media-remote handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_REMOVE_MEDIA_REMOTE,
    REPORTER_EVENTS.SET_REMOTE_DESCRIPTION,
    durationHandler(
      REPORTER_METRICS.TIME_TO_REMOVE_MEDIA_REMOTE,
      [REPORTER_OPERATION_EVENTS_MAP.REMOVE_MEDIA_REMOTE],
      false
    )
  )

  // Register the time-to-restart-media handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_RESTART_MEDIA,
    REPORTER_OPERATION_EVENTS_MAP.MEDIA_RESTART,
    durationHandler(REPORTER_METRICS.TIME_TO_RESTART_MEDIA, [REPORTER_OPERATION_EVENTS_MAP.MEDIA_RESTART], false)
  )

  // Register the time-to-send-custom-paramters handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_SEND_CUSTOM_PARAMETERS,
    REPORTER_OPERATION_EVENTS_MAP.SEND_CUSTOM_PARAMETERS,
    durationHandler(REPORTER_METRICS.TIME_TO_SEND_CUSTOM_PARAMETERS, [
      REPORTER_OPERATION_EVENTS_MAP.SEND_CUSTOM_PARAMETERS
    ])
  )

  // Register the time-to-relay-candidates handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_RELAY_CANDIDATES,
    [REPORTER_EVENTS.SET_LOCAL_DESCRIPTION, REPORTER_EVENTS.RELAY_CANDIDATE_COLLECTED],
    relayCandidatesHandler()
  )

  // Time to forward incoming call
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_FORWARD,
    [REPORTER_EVENTS.FORWARD_CALL],
    durationHandler(REPORTER_METRICS.TIME_TO_FORWARD, [REPORTER_EVENTS.FORWARD_CALL])
  )

  // Register the time-to-direct-transfer handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_DIRECT_TRANSFER,
    [REPORTER_EVENTS.DIRECT_TRANSFER],
    durationHandler(REPORTER_METRICS.TIME_TO_DIRECT_TRANSFER, [REPORTER_EVENTS.DIRECT_TRANSFER])
  )

  // Register the time-to-consultative-transfer handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_CONSULTATIVE_TRANSFER,
    [REPORTER_EVENTS.CONSULTATIVE_TRANSFER],
    durationHandler(REPORTER_METRICS.TIME_TO_CONSULTATIVE_TRANSFER, [REPORTER_EVENTS.CONSULTATIVE_TRANSFER])
  )

  // Register the time-to-join handler
  callReport.registerMetricHandler(
    REPORTER_METRICS.TIME_TO_JOIN,
    REPORTER_OPERATION_EVENTS_MAP.JOIN,
    joinedCallDurationHandler(REPORTER_METRICS.TIME_TO_JOIN, [REPORTER_OPERATION_EVENTS_MAP.JOIN])
  )
}

export default {
  handlers: {
    durationHandler,
    joinedCallDurationHandler,
    relayCandidatesHandler
  },
  registerAllMetricHandlers
}
